import React from "react";
import quotes from "../../../constants/storagePrototype/dianaInsprite"
import tool from "../../../tool"
import { chromeGet, chromeSet } from "../../../tool/storageHandle";
const styles = {//Vue scoped css
    //.cssscopedcontainercontainer
    pot: {
        position: "fixed" as "fixed",
        bottom: "0",
        left: "0",
        height: "calc(100% - 50px)",
        zIndex: 3
    },
    diana: {
        height: "calc(54% - 27px)",//(100% - 50px) * ( 588 / 1080 )
        zIndex: 4,
        cursor: "pointer"
    },
    container: {
        position: "absolute" as "absolute",
        top: "50px",
        left: "0",
        width: "100%",
        height: "calc(100% - 50px)",
        zIndex: "-1"
    },
    dialog: {
        position: "absolute" as "absolute",
        bottom: "calc(54% - 27px)",
        left: ((window.innerHeight / 1080) * 437 - 225) + "px",
        //leftnscalen+225+225*scale=632*scale,225632
        //scale
        width: "450px",
        zIndex: "5",
        transform: "scale(" + (window.innerHeight / 1080) + ")",
        transitionDuration: "0.5s"
    },
    givingHeartMeme: {
        position: "absolute" as "absolute",
        left: "0",
        bottom: "0",
        width: "100px",
        height: "100px"
    },
    quote: {
        backgroundColor: "#e799b0",
        padding: "16px",
        paddingLeft: "84px",
        marginLeft: "16px",
        fontSize: "1.5em",
        border: "#fff solid 2px",
        borderRadius: "4px",
        display: "block",
        color: "#fff",
        fontWeight: "600",
        wordWrap: "break-word" as "break-word",
    }
}
const poseArray = [require("../../../assets/images/background/diana_1.png"), require("../../../assets/images/background/diana_2.png")]
type stateType = {
    pose: number,
    dialogVisible: boolean,
    currentDialog: string,
    currentTimer: any,//AnyScript,
    autoTimer: any,
    quotes:typeof quotes,
}
class DianaTheInspirator extends React.Component<{}, stateType>{
    constructor(props: any) {
        super(props);
        this.state = {
            pose: 0,
            dialogVisible: false,
            currentDialog: "",
            currentTimer: 0,
            autoTimer: 0,
            quotes
        }
        let start: DOMHighResTimeStamp | undefined;
        let RAFfunc = (timestamp: DOMHighResTimeStamp) => {
            if (start === undefined) {
                start = timestamp;
            }
            const elapsed = timestamp - start;
            this.setState({ pose: Math.floor(elapsed / 3000) % 2 === 0 ? 0 : 1 })
            requestAnimationFrame(RAFfunc);
        }
        requestAnimationFrame(RAFfunc);
        this.pokingDiana()
    }
    componentDidMount=async()=>{
        this.setState({quotes:await chromeGet("quotes")})
        console.log(this.state)
    }
    pokingDiana = async () => {//
        const setRetrigger = () => {
            clearTimeout(this.state.autoTimer)
            this.setState({
                autoTimer: setTimeout(() => {
                    this.pokingDiana()
                }, 70000)
            })
        }
        //
        if (document.hasFocus())
            if (this.state.currentTimer == 0) {//
                const currentTime = tool.getTime();
                const hNow = Number(currentTime[1]);//
                let timeToRequest: "morning" | "noon" | "evening" | "night" | "none" = "none";
                let storageDate = await chromeGet("date")
                if (storageDate != currentTime[0]) {//
                    await chromeSet({
                        date: currentTime[0],
                        morning: false,
                        noon: false,
                        evening: false,
                        night: false,
                        notice: Date.now()
                    });
                }
                const setDialog = (dialog: string) => {//
                    this.setState({//20
                        currentTimer: setTimeout(() => {//20
                            this.setState({
                                dialogVisible: false,
                                currentTimer: 0,//
                            });
                        }, 12000),
                        dialogVisible: true,
                        currentDialog: dialog,
                    });
                    return;//
                }
                //
                if (hNow >= 6 && hNow <= 9) {
                    timeToRequest = "morning";
                } else if (hNow >= 11 && hNow <= 13) {
                    timeToRequest = "noon";
                } else if (hNow >= 17 && hNow <= 21) {
                    timeToRequest = "evening";
                } else if (hNow >= 21 || hNow <= 3) {
                    timeToRequest = "night";
                }
                if (timeToRequest != "none") {//
                    const requestRes = await chromeGet(timeToRequest);
                    if (!requestRes) {
                        setDialog(this.state.quotes[timeToRequest]);
                        let temp: { [key: string]: boolean } = {};
                        temp[timeToRequest] = true
                        chromeSet(temp);
                        setRetrigger()
                        return;//
                    }
                }
                //
                const requestRes = await chromeGet("notice");
                const noticeTime = await chromeGet("noticeTime")
                if (Date.now() - requestRes >= noticeTime) {
                    setDialog(this.state.quotes.notice[Math.floor(Math.random() * this.state.quotes.notice.length)])//01
                    await chromeSet({ notice: Date.now() });
                    setRetrigger()
                    return;
                }
                //
                setDialog(this.state.quotes.daily[Math.floor(Math.random() * this.state.quotes.daily.length)]);
                setRetrigger()
                return;
            } else {//
                this.setState({ dialogVisible: false, currentTimer: 0 });//ACDebug
            }
        setRetrigger()
    }
    render(): React.ReactNode {
        return (
            <div style={styles.container}>
                <div style={Object.assign({}, styles.dialog, { opacity: this.state.dialogVisible ? "1" : "0" })}>
                    <img src={require("../../../assets/images/background/diana_meme.png")} alt="" style={styles.givingHeartMeme} />
                    <p style={styles.quote}>{this.state.currentDialog}</p>
                </div>
                <img src={require("../../../assets/images/background/flower_pot.png")} alt="" style={styles.pot} />
                <img src={poseArray[this.state.pose]} alt="" style={Object.assign({}, styles.pot, styles.diana)} onClick={this.pokingDiana} />
            </div>
        )
    }
}
export default DianaTheInspirator;